@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
.currency-container{
    display: flex;
    justify-content: center;
}

.chart-row{
    display: flex;
    flex-direction: row;
    justify-content: start;
    align-items: center;
    height: 80px;
    border-bottom: 1px solid #d7d7d7;
    width: 900px;
}
.coin{
    display: flex;
    align-items:  center;
    padding-right:  24px;
    min-width: 300px;
}

.coin h2{
    font-size: 16px;
    width: 150px;
  }
  
  .coin img{
    height: 30px;
    width: 30px;
    margin-right: 10px;
  }
  .coin-symbol{
      text-transform: uppercase;
  }

  .coin-data{
    display: flex;
    text-align: center;
    justify-content:  space-between;
  }
  
  .coin-price{
    width: 210px;
  }
  
  .coin-volume{
    width: 230px;
  }
  
  .coin-marketcap {
    width: 155px;
  }
  .coin-percent{
      width: 100%;
  }

  .red{
      color: red;
  }

  .green{
      color: green;
  }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  background-color: #1a1a1c;
  color: #fff;
}

main{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 64px;
  color: white;
}

.crypto-search {
  margin-bottom: 64px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
}

.crypto-search h1 {
  margin-bottom: 32px;
  text-align: center;
}

.search-input {
  padding-left: 16px;
  width: 300px;
  height: 50px;
  border-radius: 4px;
  border: none;
  background-image: linear-gradient(
    -225deg,
    #ac32e4 0%,
    #7918f2 48%,
    #4801ff 100%
  );

  color: #e2e2e2;
}

.search-input::-webkit-input-placeholder {
  color: #e2e2e2;
}

.search-input:-ms-input-placeholder {
  color: #e2e2e2;
}

.search-input::placeholder {
  color: #e2e2e2;
}
